import React from "react";
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Card,
  CardTitle,
  CardText,
  Row,
  Col
} from "reactstrap";
import classnames from "classnames";
import { data } from "../data";

export default class Tabs extends React.Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      activeTab: 1,
      technologies: [
        "REACTJS",
        "REACT NATIVE",
        "JAVASCRIPT",
        "NODEJS",
        "WORDPRESS",
        "BOOTSTRAP"
      ]
    };
  }
  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }
  render() {
    return (
      <div className="m-5 pb-5">
        <Nav pills className="nav justify-content-center">
          {this.state.technologies.map((tech, index) => {
            return (
              <NavItem key={index}>
                <NavLink
                  href="#"
                  className={classnames({
                    active: this.state.activeTab === index + 1
                  })}
                  onClick={() => {
                    this.toggle(index + 1);
                  }}
                >
                  {tech}
                </NavLink>
              </NavItem>
            );
          })}
        </Nav>
        <TabContent activeTab={this.state.activeTab}>
          {this.state.technologies.map((tech, index) => {
            const dataFiltered = data.filter(d => d.tech === tech);
            return (
              <TabPane tabId={index + 1} key={index}>
                <Row className="mt-3">
                  {dataFiltered.map((data, i) => {
                    return (
                      <Col sm="6" key={i}>
                        <Card body className="m-2">
                          <CardTitle>{data.title}</CardTitle>
                          <CardText>{data.text}</CardText>
                          <a
                            target="_blank"
                            rel="noopener noreferrer"
                            href={data.site}
                            className={
                              data.siteDisabled
                                ? "btn btn-info disabled"
                                : "btn btn-info"
                            }
                            role="button"
                          >
                            Le site
                          </a>
                          <a
                            target="_blank"
                            rel="noopener noreferrer"
                            href={data.repo}
                            className={
                              data.repoDisabled
                                ? "btn btn-secondary mt-1 disabled"
                                : "btn btn-secondary mt-1"
                            }
                            role="button"
                            disabled={data.repoDisabled}
                          >
                            Repo GITHUB
                          </a>
                        </Card>
                      </Col>
                    );
                  })}
                </Row>
              </TabPane>
            );
          })}
        </TabContent>
      </div>
    );
  }
}
